<template><section><h1>PolygonGraphics</h1>
<p>The <code v-pre>vc-graphics-polygon</code> component is used to load a polygon that defined by an hierarchy of linear rings which make up the outer shape and any nested holes. The polygon conforms to the curvature of the globe and can be placed on the surface or at altitude and can optionally be extruded into a volume.</p>
<h2>Examples</h2>
<h3>Load PolygonGraphics with <code v-pre>vc-graphics-polygon</code></h3>
<h4>Preview</h4>
<doc-preview>
<template>
  <div class="viewer">
    <vc-viewer @ready="ready">
      <vc-entity :description="description" :polygon.sync="polygon1">
        <vc-graphics-polygon ref="polygon1" :hierarchy="hierarchy1" :material="material1"></vc-graphics-polygon>
      </vc-entity>
      <vc-entity :description="description" :polygon.sync="polygon2">
        <vc-graphics-polygon :hierarchy="hierarchy2" :material="material2" :extrudedHeight="500000.0" :closeTop="false" :closeBottom="false" ref="polygon2"></vc-graphics-polygon>
      </vc-entity>
      <vc-entity :description="description" :polygon.sync="polygon3">
        <vc-graphics-polygon :hierarchy="hierarchy3" :material="material3" :extrudedHeight="0" :perPositionHeight="true" :outline="true" :outlineColor="outlineColor3" ref="polygon3"></vc-graphics-polygon>
      </vc-entity>
      <vc-entity :description="description" :polygon.sync="polygon4">
        <vc-graphics-polygon :hierarchy="hierarchy4" :material="material4" :height="0" :outline="true" ref="polygon4"></vc-graphics-polygon>
      </vc-entity>
      <vc-entity :description="description" :polygon.sync="polygon5">
        <vc-graphics-polygon :hierarchy="hierarchy5" :material="material5" :perPositionHeight="true" :outline="true" :outlineColor="outlineColor5" ref="polygon5"></vc-graphics-polygon>
      </vc-entity>
      <vc-entity :description="description" :polygon.sync="polygon6">
        <vc-graphics-polygon :hierarchy="hierarchy6" :material="material6" :extrudedHeight="50000" :outline="true" :outlineColor="outlineColor6" ref="polygon6"></vc-graphics-polygon>
      </vc-entity>
    </vc-viewer>
  </div>
</template>

