        ],
        material1: 'red',

        polygon2: {},
        hierarchy2: [],
        material2: 'GREEN',

        polygon3: {},
        hierarchy3: [],
        material3: {},
        outlineColor3: {},

        polygon4: {},
        hierarchy4: {
          positions: [
            { lng: -99.0, lat: 30.0 },
            { lng: -85.0, lat: 30.0 },
            { lng: -85.0, lat: 40.0 },
            { lng: -99.0, lat: 40.0 }
          ],
          holes: [
            {
              positions: [
                { lng: -97.0, lat: 31.0 },
                { lng: -97.0, lat: 39.0 },
                { lng: -87.0, lat: 39.0 },
                { lng: -87.0, lat: 31.0 }
              ],
              holes: [
                {
                  positions: [
                    { lng: -95.0, lat: 33.0 },
                    { lng: -89.0, lat: 33.0 },
                    { lng: -89.0, lat: 37.0 },
                    { lng: -95.0, lat: 37.0 }
                  ],
                  holes: [
                    {
                      positions: [
                        { lng: -93.0, lat: 34.0 },
                        { lng: -91.0, lat: 34.0 },
                        { lng: -91.0, lat: 36.0 },
                        { lng: -93.0, lat: 36.0 }
                      ]
                    }
                  ]
                }
              ]
            }
          ]
        },
        material4: {},

        polygon5: {},
        hierarchy5: [],
        material5: {},
        outlineColor5: 'BLACK',

        polygon6: {},
        hierarchy6: [],
        material6: {},
        arcType6: {},
        outlineColor6: {}
      }
    },
    mounted() {
      Promise.all([
        this.$refs.polygon1.createPromise,
        this.$refs.polygon2.createPromise,
        this.$refs.polygon3.createPromise,
        this.$refs.polygon4.createPromise,
        this.$refs.polygon5.createPromise,
        this.$refs.polygon6.createPromise
      ]).then((instances) => {
        instances[0].viewer.zoomTo(instances[0].viewer.entities)
      })
    },
    methods: {
      ready(cesiumInstance) {
        const { Cesium, viewer } = cesiumInstance

        this.hierarchy2 = [
          { lng: -108.0, lat: 42.0 },
          { lng: -100.0, lat: 42.0 },
          { lng: -104.0, lat: 40.0 }
        ]

        this.hierarchy3 = [
          { lng: -108.0, lat: 25.0, height: 100000 },
          { lng: -100.0, lat: 25.0, height: 100000 },
          { lng: -100.0, lat: 30.0, height: 100000 },
          { lng: -108.0, lat: 30.0, height: 100000 }
        ]

        this.materia3 = Cesium.Color.ORANGE.withAlpha(0.5)

        this.material4 = Cesium.Color.BLUE.withAlpha(0.5)

        this.material5 = Cesium.Color.CYAN.withAlpha(0.5)
        this.hierarchy5 = Cesium.Cartesian3.fromDegreesArrayHeights([-90.0, 41.0, 0.0, -85.0, 41.0, 500000.0, -80.0, 41.0, 0.0])

        this.hierarchy6 = [
          { lng: -120.0, lat: 45.0 },
          { lng: -80.0, lat: 45.0 },
          { lng: -80.0, lat: 55.0 },
          { lng: -120.0, lat: 55.0 }
        ]
        this.material6 = Cesium.Color.PURPLE
        this.outlineColor6 = Cesium.Color.MAGENTA
      }
    }
  }
</script>